import {request} from '@/utils/request'

export function fetchList(query) {
  return request({
    url: '/Supervisor/GetList',
    method: 'get',
    params: query
  })
}

export function fetchPageList(query) {
  return request({
    url: '/Supervisor/GetPageList',
    method: 'get',
    params: query
  })
}

export function fetchPersonnel(Id) {
  return request({
    url: '/Supervisor/GetForm',
    method: 'get',
    params: { Id }
  })
}

export function fetchProject(id) {
  return request({
    url: '/vue-element-admin/personnel/project',
    method: 'get',
    params: { id }
  })
}

export function createPersonnel(data) {
  return request({
    url: '/vue-element-admin/personnel/create',
    method: 'post',
    data
  })
}

export function updatePersonnel(data) {
  return request({
    url: '/vue-element-admin/personnel/update',
    method: 'post',
    data
  })
}
export function savePersonnel(data) {
  return request({
    url: '/Supervisor/SaveForm',
    method: 'post',
    data
  })
}
export function recoveryPersonnel(data) {
  return request({
    url: '/Supervisor/RecoveryForm',
    method: 'post',
    data
  })
}
export function blacklistPersonnel(data) {
  return request({
    url: '/Supervisor/BlacklistForm',
    method: 'post',
    data
  })
}
export function deletePersonnel(data) {
  return request({
    url: '/Supervisor/DeleteForm',
    method: 'post',
    data
  })
}
