import store from '@/store'

/**
 * @param {Array} value
 * @returns {Boolean}
 * @example see @/views/permission/directive.vue
 */
export default function checkPermission(Authorize) {
  if (Authorize) {
    const menus = store.getters && store.getters.menus
    const hasPermission = menus.some(menu => {
      return Authorize===menu.Authorize
    })
    return hasPermission
  } else {
    console.error(`need Authorize! Like v-permission="['admin','editor']"`)
    return false
  }
}
