                  :value="index"
                />
              </el-select>
              <el-input v-else :value="genderOptions[temp.Sex]" readonly />
            </el-form-item>
            <el-form-item label="联系方式" prop="Tel">
              <el-input
                v-model="temp.Tel"
                :readonly="dialogStatus === 'detail'"
                maxlength="11"
                show-word-limit
              />
            </el-form-item>
            <el-form-item label="证书码" prop="CertificateId">
              <el-input
