              >
                <el-option
                  v-for="item in Posts"
                  :key="item.DictKey"
                  :label="item.DictValue"
                  :value="item.DictKey"
                />
              </el-select>
              <el-input v-else :value="getPostName(temp.PostCode)" readonly />
            </el-form-item>
          </el-col>
          <el-col :span="10" :offset="1" class="upload-container">
            <el-form-item label="身份证正面" prop="CardFrontPhoto">
              <el-upload
                :disabled="dialogStatus === 'detail'"
                class="avatar-uploader"
                list-type="picture-card"
                :action="uploadUrl"
                ref="cardFrontUploader"
                name="file"
                :limit="1"
                :headers="headers"
                :show-file-list="false"
                :auto-upload="true"
                :on-preview="handlePreview"
                :on-success="handleIdentityCardFrontSuccess"
                :on-error="handleIdentityCardFrontError"
                :on-change="handleIdentityCardFrontChange"
                :before-upload="beforeAvatarUpload"
                :before-remove="beforeRemove"
                :on-remove="handleRemove"
                :on-exceed="handleExceed"
              >
                <el-image
                  v-if="temp.CardFrontPhotoUrl"
                  :src="temp.CardFrontPhotoUrl"
                  class="avatar"
                >
                  <div slot="error" class="image-slot">
                    <i class="el-icon-picture-outline"></i>
                  </div>
                </el-image>
                <i
                  v-else-if="dialogStatus === 'detail'"
                  class="el-icon-picture-outline avatar-uploader-icon"
                ></i>
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="身份证反面" prop="CardBackPhoto">
              <el-upload
                :disabled="dialogStatus === 'detail'"
                class="avatar-uploader"
                list-type="picture-card"
                :action="uploadUrl"
                ref="cardBackUploader"
                name="file"
                :limit="1"
                :headers="headers"
                :show-file-list="false"
                :auto-upload="true"
                :on-preview="handlePreview"
                :on-success="handleIdentityCardBackSuccess"
                :on-error="handleIdentityCardBackError"
                :on-change="handleIdentityCardBackChange"
                :before-upload="beforeAvatarUpload"
                :before-remove="beforeRemove"
                :on-remove="handleRemove"
                :on-exceed="handleExceed"
              >
                <el-image
                  v-if="temp.CardBackPhotoUrl"
                  :src="temp.CardBackPhotoUrl"
                  class="avatar"
                >
                  <div slot="error" class="image-slot">
                    <i class="el-icon-picture-outline"></i>
                  </div>
                </el-image>
                <i
                  v-else-if="dialogStatus === 'detail'"
                  class="el-icon-picture-outline avatar-uploader-icon"
                ></i>
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="监理证书" prop="Certificatephoto">
              <el-upload
                :disabled="dialogStatus === 'detail'"
                class="avatar-uploader"
                list-type="picture-card"
                :action="uploadUrl"
                ref="certificatephotoUploader"
                name="file"
                :limit="1"
                :headers="headers"
                :show-file-list="false"
                :auto-upload="true"
                :on-preview="handlePreview"
                :on-success="handleSupervisionCertificateSuccess"
                :on-error="handleSupervisionCertificateError"
                :on-change="handleSupervisionCertificateChange"
                :before-upload="beforeAvatarUpload"
                :before-remove="beforeRemove"
                :on-remove="handleRemove"
                :on-exceed="handleExceed"
              >
                <el-image
                  v-if="temp.CertificatephotoUrl"
                  :src="temp.CertificatephotoUrl"
                  class="avatar"
                >
                  <div slot="error" class="image-slot">
                    <i class="el-icon-picture-outline"></i>
                  </div>
                </el-image>
                <i
                  v-else-if="dialogStatus === 'detail'"
                  class="el-icon-picture-outline avatar-uploader-icon"
                ></i>
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>

      <div v-if="dialogStatus !== 'detail'" slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> 取消 </el-button>
        <el-button
          type="primary"
          @click="dialogStatus === 'create' ? createData() : updateData()"
        >
          {{ active == 0 && dialogStatus === "create" ? "下一步" : "确定" }}
        </el-button>
      </div>
    </el-dialog>

    <el-dialog
      :visible.sync="dialogProjectVisible"
      title="在监项目"
      width="70%"
    >
      <el-table
        :data="projectData"
        border
        fit
        highlight-current-row
        style="width: 100%"
      >
        <el-table-column label="序号" type="index" align="center" width="80">
        </el-table-column>
        <el-table-column prop="ProjName" label="项目名称" align="center" />
        <el-table-column
          prop="ProjectTypeId"
