          align="center"
        />
        <el-table-column
          prop="StatusName"
          label="项目状态"
          width="80"
          align="center"
        >
          <template slot-scope="{ row }">
            <el-tag :type="row.StatusName">
              {{ row.StatusName }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="ProjArea" label="区域" align="center">
        </el-table-column>
        <el-table-column prop="ProjAddr" label="地点" align="center" />
        <el-table-column
          v-if="checkPermission(['PersonnelCompany'])"
          prop="supervisor"
          label="监理单位"
          align="center"
        />
        <el-table-column
          v-if="checkPermission(['editor'])"
          prop="Contact"
          width="80"
          label="负责人"
          align="center"
        />
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogProjectVisible = false"
          >确定</el-button
        >
      </span>
    </el-dialog>
    <el-dialog :visible.sync="dialogVisible" title="">
      <el-image :src="dialogImageUrl">
        <div slot="error" class="image-slot">
          <i class="el-icon-picture-outline"></i>
        </div>
      </el-image>
    </el-dialog>
  </div>
</template>

<script>
import { baseURL } from "@/utils/request";
import { validPhoneNumber, validID } from "@/utils/validate";
import { getToken } from "@/utils/auth";
import { mapGetters } from "vuex";
import {
  fetchList,
  fetchPageList,
  fetchProject,
  savePersonnel,
  recoveryPersonnel,
  blacklistPersonnel,
  deletePersonnel,
} from "@/api/personnel";
import { getFileUrl, saveImg } from "@/api/file";
import waves from "@/directive/waves"; // waves directive
import permission from "@/directive/permission/index.js"; // 权限判断指令
import checkPermission from "@/utils/permission"; // 权限判断函数
import { parseTime } from "@/utils";
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination

export default {
  name: "Personnel",
  components: { Pagination },
  directives: { waves, permission },
  filters: {
    statusFilter(Status) {
      const statusMap = {
        正常: "success",
        已过期: "danger",
      };
      return statusMap[Status];
    },
  },
  props: {
    beblack: {
      type: Number,
      default: 0,
    },
  },
  computed: {
    ...mapGetters(["dicts"]),
  },
  data() {
    return {
      headers: {
        // "Content-Type": "multipart/form-data",
        Authorization: "Bearer " + getToken(),
      },
      active: 0,
      tableKey: 0,
      dialogImageUrl: "",
      dialogVisible: false,
      uploadUrl: "",
      list: null,
      total: 0,
      listLoading: true,
      listQuery: {
        PageIndex: 1,
        Beblack: this.beblack,
        PageSize: 20,
        PostCode: undefined,
        CertificateStatusName: undefined,
        Name: undefined,
        type: undefined,
        BaseIsDelete: 0,
        CertificateStatusName: "",
      },
      statusOptions: ["正常", "过期"],
      genderOptions: ["女", "男"],
      Posts: [],
      sortOptions: [
        { label: "ID 升序", key: "+id" },
        { label: "ID 降序", key: "-id" },
      ],
      temp: {
        Id: 0,
        UserId: "",
        SuperviseId: "",
        Name: "",
        Sex: 0,
        Tel: "",
        PostCode: "",
        CardId: "",
        CardFrontPhoto: "",
        CardFrontPhotoUrl: "",
        CardBackPhoto: "",
        CardBackPhotoUrl: "",
        Certificatephoto: "",
        CertificatephotoUrl: "",
        CertificateId: "",
        Validityperiod: "",
        Beblack: 0,
        Status: 1,
        CertificateStatusName: "",
      },
      dialogFormVisible: false,
      dialogStatus: "",
      textMap: {
        update: "人员编辑",
        create: "人员新增",
        detail: "人员详情",
      },
      dialogProjectVisible: false,
      projectData: [],
      baseDataRules: {
        Name: [{ required: true, message: "姓名为必填项", trigger: "blur" }],
        CardId: [
          {
            required: true,
            message: "身份证号格式不正确",
            trigger: "blur",
            validator: (rule, value, callback) => {
              if (value && !validID(value)) {
                callback(new Error("请输入身份证号码"));
              } else {
                callback();
              }
            },
          },
        ],
      },
      detailDataRules: {
        Sex: {
          required: true,
          message: "性别为必填项",
          trigger: "change",
        },
        PostCode: {
          required: true,
          message: "岗位为必填项",
          trigger: "change",
        },
        CardFrontPhoto: {
          required: true,
          message: "请上传身份证正面",
          trigger: "blur",
        },
        CardBackPhoto: {
          required: true,
          message: "请上传身份证反面",
          trigger: "blur",
        },
        Certificatephoto: {
          required: true,
          message: "请上传资质证书",
          trigger: "blur",
        },
        type: [
          { required: true, message: "type is required", trigger: "change" },
        ],
        CertificateId: {
          required: true,
          message: "证书码为必填项",
          trigger: "blur",
        },
        Validityperiod: {
          required: true,
          message: "证书有效期为必填项",
          trigger: "change",
        },
        Name: [{ required: true, message: "姓名为必填项", trigger: "blur" }],
        Tel: [
          {
            required: true,
            message: "手机号格式不正确",
            trigger: "blur",
            validator: (rule, value, callback) => {
              if (value && !validPhoneNumber(value)) {
                callback(new Error("请输入正确的手机号"));
              } else {
                callback();
              }
            },
          },
        ],
      },
      downloadLoading: false,
    };
  },
  watch: {
    beblack(newValue, oldValue) {
      console.log("newValue", newValue);
    },
  },
  created() {
    this.uploadUrl = baseURL + "/File/UploadImg";
    let obj = this.dicts.find((dict) => dict.DictType == "Post");
    if (obj) {
      this.Posts = obj.Detail;
    }
    this.getList();
  },
  mounted() {},
  methods: {
    checkPermission,
    getPostName(postCode) {
      let post = this.Posts.find((post) => post.DictKey == postCode);
      return post ? post.DictValue : "";
    },
    getList() {
      this.listLoading = true;
      fetchPageList(this.listQuery)
        .then((response) => {
          this.list = response.Data;
          this.total = response.Total;
          this.listLoading = false;
        })
        .catch((error) => {
          this.listLoading = false;
        });
    },
    handleFilter() {
      this.listQuery.PageIndex = 1;
      this.getList();
    },

    resetTemp() {
      this.temp = {
        Id: 0,
        UserId: "",
        SuperviseId: "",
        Name: "",
        Sex: 0,
        Tel: "",
        PostCode: "",
        CardId: "",
        CardFrontPhoto: "",
        CardFrontPhotoUrl: "",
        CardBackPhoto: "",
        CardBackPhotoUrl: "",
        Certificatephoto: "",
        CertificatephotoUrl: "",
        CertificateId: "",
        Validityperiod: "",
        Beblack: 0,
        Status: 1,
        CertificateStatusName: "",
      };
    },
    saveImg(temp) {
      if (
        !temp.createSavedImg &&
        temp.CardBackFileId &&
        temp.CardFrontFileId &&
        temp.CertificateFileId
      ) {
        return new Promise((resolve, reject) => {
          Promise.all([
            saveImg({
              Id: 0,
              FileId: temp.CardBackFileId,
              FileType: 2,
              FileName: temp.CardBackPhotoFileName,
              FileSize: temp.CardBackPhotoFileSize,
            }),
            saveImg({
              Id: 0,
              FileId: temp.CardFrontFileId,
              FileType: 2,
              FileName: temp.CardFrontPhotoFileName,
              FileSize: temp.CardFrontPhotoFileSize,
            }),
            saveImg({
              Id: 0,
              FileId: temp.CertificateFileId,
              FileType: 2,
              FileName: temp.CertificatephotoFileName,
              FileSize: temp.CertificatephotoFileSize,
            }),
          ])
            .then((result) => {
              console.log("准备工作完毕123");
              console.log(result);
              temp.CardBackPhoto = result[0].Data;
              temp.CardFrontPhoto = result[1].Data;
              temp.Certificatephoto = result[2].Data;
              temp.createSavedImg = true;
              resolve(temp);
            })
            .catch(() => {
              reject();
            });
        });
      } else if (
        !temp.createSavedImg &&
        !temp.CardBackFileId &&
        temp.CardFrontFileId &&
        temp.CertificateFileId
      ) {
        return new Promise((resolve, reject) => {
          Promise.all([
            saveImg({
              Id: 0,
              FileId: temp.CardFrontFileId,
              FileType: 2,
              FileName: temp.CardFrontPhotoFileName,
              FileSize: temp.CardFrontPhotoFileSize,
            }),
            saveImg({
              Id: 0,
              FileId: temp.CertificateFileId,
              FileType: 2,
              FileName: temp.CertificatephotoFileName,
              FileSize: temp.CertificatephotoFileSize,
            }),
          ])
            .then((result) => {
              console.log("准备工作完毕123");
              console.log(result);
              temp.CardFrontPhoto = result[0].Data;
              temp.Certificatephoto = result[1].Data;
              temp.createSavedImg = true;
              resolve(temp);
            })
            .catch(() => {
              reject();
            });
        });
      } else if (
        !temp.createSavedImg &&
        temp.CardBackFileId &&
        !temp.CardFrontFileId &&
        temp.CertificateFileId
      ) {
