    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        if (this.active == 0) {
          this.$refs["baseDataForm"].clearValidate();
        } else {
          this.$refs["detailDataForm"].clearValidate();
        }
      });
    },
    createData() {
      if (this.active === 0) {
        this.$refs["baseDataForm"].validate((valid) => {
          if (valid) {
            let { Name, CardId } = this.temp;
            fetchList({ Name, CardId }).then((response) => {
              console.log("fetchList", response);
              let data = response.Data || [];
              if (data && data.length > 0) {
                if (data[0].Beblack == 1) {
                  //是黑名单
                  this.$notify({
                    title: "黑名单",
                    message: `${Name}已进入黑名单，不能添加。`,
                    type: "warning",
                    duration: 2000,
                  });
                } else {
                  this.$confirm(
                    `${Name}已存在监理人员库，是否直接带出该监理人员的信息？`,
                    "提示",
                    {
                      confirmButtonText: "是",
                      cancelButtonText: "否",
                      type: "info",
                    }
                  )
                    .then((_) => {
                      console.log("tem0", this.temp);
                      this.temp = Object.assign({}, data[0]);
                      console.log("temp1", this.temp);
                      this.active++;
                    })
                    .catch((_) => {
                      this.active++;
                    });
                }
              } else {
                this.active++;
              }
            });
          }
        });
      } else {
        this.saveImg(this.temp).then((res) => {
          this.$refs["detailDataForm"].validate((valid) => {
            if (valid) {
              savePersonnel(res)
                .then(() => {
                  this.getList();
                  this.dialogFormVisible = false;
                  this.$notify({
                    title: "成功",
                    message: "新增成功！",
                    type: "success",
                    duration: 2000,
                  });
                  this.active = 0;
                })
                .catch((error) => {
                  this.active = 0;
                  this.$notify({
                    title: "失败",
                    message: "新增失败！",
                    type: "error",
                    duration: 2000,
                  });
                });
            }
          });
        });
      }
    },
    handleAction(row, type) {
      console.log("row", row);
      let data = {
        IdS: `${row.CardFrontPhoto},${row.CardBackPhoto},${row.Certificatephoto}`,
      };
      console.log("data", data);
      getFileUrl(data)
        .then((response) => {
          let data = response.Data;
          this.temp = Object.assign({}, row); // copy obj
          if (data && data.length > 0) {
            let CardFrontPhotoUrl = data.find((_) => _.Id == row.CardFrontPhoto)
              .FileId;
            this.$set(
              this.temp,
              "CardFrontPhotoUrl",
              baseURL + "/" + CardFrontPhotoUrl
            );

            let CardBackPhotoUrl = data.find((_) => _.Id == row.CardBackPhoto)
              .FileId;
            this.$set(
              this.temp,
              "CardBackPhotoUrl",
              baseURL + "/" + CardBackPhotoUrl
            );

            let CertificatephotoUrl = data.find(
              (_) => _.Id == row.Certificatephoto
            ).FileId;
            this.$set(
              this.temp,
              "CertificatephotoUrl",
              baseURL + "/" + CertificatephotoUrl
            );
          }
          this.dialogStatus = type;
          this.dialogFormVisible = true;
          this.$nextTick(() => {
            this.$refs["detailDataForm"].clearValidate();
          });
        })
        .catch();
    },
    updateData() {
      this.$refs["detailDataForm"].validate((valid) => {
        if (valid) {
          let temp = Object.assign({}, this.temp);
          this.saveImg(temp).then((res) => {
            savePersonnel(res)
              .then(() => {
                this.getList();
                this.dialogFormVisible = false;
                this.$notify({
                  title: "成功",
                  message: "新增成功！",
                  type: "success",
                  duration: 2000,
                });
              })
              .catch((error) => {
                this.$notify({
                  title: "失败",
                  message: "新增失败！",
                  type: "error",
                  duration: 2000,
                });
              });
          });
        }
      });
    },
    handleModifyStatus(row, type) {
      let message =
        type == 0 ? "确定要恢复正常吗?" : "确定要执行加入黑名单操作吗?";
      this.$confirm(message, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then((_) => {
          if (type == 0) {
            recoveryPersonnel({ Id: row.Id }).then((response) => {
              this.$notify({
                title: "成功",
                message: "操作成功",
                type: "success",
                duration: 2000,
              });
              this.getList();
            });
          } else {
            blacklistPersonnel({ Id: row.Id }).then((response) => {
              this.$notify({
                title: "成功",
                message: "操作成功",
                type: "success",
                duration: 2000,
              });
              this.getList();
            });
          }
        })
        .catch((_) => {});
    },
